export default class Global {
    constructor () {};

    /**
     * Âà§Êñ≠ÊòØÂê¶‰∏∫ PC Á´ØÔºåËã•ÊòØÂàôËøîÂõû trueÔºåÂê¶ÂàôËøîÂõû flase
     */
    IsPC() {
        let userAgentInfo = navigator.userAgent,
            flag = true,
            Agents = ["Android", "iPhone","SymbianOS", "Windows Phone","iPad", "iPod"];

        for (let v = 0; v < Agents.length; v++) {
            if (userAgentInfo.indexOf(Agents[v]) > 0) {
                flag = false;
                break;
            }
        }
        return flag;
    };

    /**
     * ÁºìÂä®ÂáΩÊï∞ÔºåÁî±Âø´Âà∞ÊÖ¢
     * @param {Num} t ÂΩìÂâçÊó∂Èó¥
     * @param {Num} b ÂàùÂßãÂÄº
     * @param {Num} c ÂèòÂåñÂÄº
     * @param {Num} d ÊåÅÁª≠Êó∂Èó¥
     */
    easeOut(t, b, c, d) {
        if ((t /= d / 2) < 1) return c / 2 * t * t + b;
        return -c / 2 * ((--t) * (t - 2) - 1) + b;
    };

    windowToCanvas(canvas, e) {
        let bbox = canvas.getBoundingClientRect(),
            x = this.IsPC() ? e.clientX || event.clientX : e.changedTouches[0].clientX,
            y = this.IsPC() ? e.clientY || event.clientY : e.changedTouches[0].clientY;
            
        return {
            x: x - bbox.left,
            y: y - bbox.top
        }
    };

    /**
     * ÁªòÂà∂Ëá™Âä®Êç¢Ë°åÁöÑÊñáÊú¨
     * @param {Obj} context
     * @param {Str} t          ÊñáÊú¨ÂÜÖÂÆπ
     * @param {Num} x          ÂùêÊ†á
     * @param {Num} y          ÂùêÊ†á
     * @param {Num} w          ÊñáÊú¨ÈôêÂà∂ÂÆΩÂ∫¶
     * @param {Num} lineHeight Ë°åÈ´ò
     */
    drawText(context, t, x, y, w, lineHeight = 20){
        let chr = t.split(''),
            temp = '',           
            row = [];

        for (let a = 0; a < chr.length; a++){
            if ( context.measureText(temp).width < w ) {
                ;
            }
            else{
                row.push(temp);
                temp = '';
            }
            temp += chr[a];
        };

        row.push(temp);

        for(let b = 0; b < row.length; b++){
            context.fillText(row[b], x, y + (b + 1) * lineHeight);
        };
    };

    /**
     * ÂÆö‰πâÂúÜËßíÁü©ÂΩ¢ÁöÑÊñπÊ≥ï
     * @param {Obj} context
     * @param {Num} cornerX 
     * @param {Num} cornerY 
     * @param {Num} width 
     * @param {Num} height 
     * @param {Num} cornerRadius 
     */
    roundedRect(context, cornerX, cornerY, width, height, cornerRadius) {
        if (width > 0) context.moveTo(cornerX + cornerRadius, cornerY);
        else           context.moveTo(cornerX - cornerRadius, cornerY);

        context.arcTo(cornerX + width, cornerY,
            cornerX + width, cornerY + height,
            cornerRadius);

        context.arcTo(cornerX + width, cornerY + height,
            cornerX, cornerY + height,
            cornerRadius);

        context.arcTo(cornerX, cornerY + height,
            cornerX, cornerY,
            cornerRadius);

        if (width > 0) {
            context.arcTo(cornerX, cornerY,
                cornerX + cornerRadius, cornerY,
                cornerRadius);
        }
        else {
            context.arcTo(cornerX, cornerY,
                cornerX - cornerRadius, cornerY,
                cornerRadius);
        }
    }
}